


@media screen and (max-width: 992px){
  .workers-grid{
    .worker-card{
      br{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .workers-grid{
    gap: .5em;
    grid-template-columns: repeat(2,calc(50% - .25em));
    .worker-card{
      &__title{
        font-size: 1em;
      }
      &__desc{
        font-size: .8125em;
        line-height: 123%;
        margin-top: .45em;
      }
    }
  }
}
